.sidebar-block__wrapper {
  width: 100%;
  text-align: center;
  padding: 0 2.25rem;
  margin-bottom: 1.5rem;
  letter-spacing: 1px; }
  @media (min-width: 1024px) {
    .sidebar-block__wrapper {
      padding: 0 4.5rem;
      margin-bottom: 2.25rem; }
      .sidebar-block__wrapper:last-child {
        margin-bottom: 0; } }

.sidebar-block__section-title {
  display: block;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.125rem;
  letter-spacing: 3px;
  margin: 0 2.25rem 1.125rem;
  padding-bottom: 0.375rem;
  border-bottom: 1px solid var(--base-1); }



